/* =========================================================================
   Typography
   ========================================================================= */

/* General
   ========================================================================= */

/* Root element 100% font size */
html {
  font-size: 100%;
}

/* Assign body PT Sans font and slightly larger font size/line height for readability */
body {
  font-family: $pt-sans;
  font-size: 125%;
  line-height: 1.5;
}

/* Give headings Ubuntu font and semi-bold font weight */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $ubuntu;
  font-weight: 500;
}

/* Paragraph font size */
p {
  font-size: 1.125rem;

  /* Increase font size on medium and up devices */
  @include breakpoint($mq-medium) {
    font-size: 1.25rem;
  }
}

/* Make strong elements bold and slightly spaced out for readability */
strong {
  font-weight: 700;
  letter-spacing: .5px;
}

/* Make em elements italicized */
em {
  font-style: italic;
}
